.item {
  width: 32.5%;
  @media screen and (max-width: 600px) {
    width: 100%;
    margin-bottom: 15px;
  }


  .imgBlock {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .wrapperImg {
      display: flex;
      justify-content: center;
      align-items: center;
      &:hover {
        & img {
          transform: scale(0.95);
        }
      }
      & img {
        transition: 0.3s ease-in-out;
        width: 100%;
        height: 100%;
      }
    }
  }
}